<template>
  <div>
    <label for="searchName">
      名称<input type="text" id="searchName" v-model="searchName">&nbsp;
    </label>
    <label for="searchAuthor">
      作者<input type="text" id="searchAuthor" v-model="searchAuthor">&nbsp;
    </label>
    <label>
      价格<input type="number" id="searchPriceLow" v-model="searchPriceLow">
      - <input type="number" id="searchPriceHigh" v-model="searchPriceHigh">&nbsp;
    </label>
    <button @click="searchBook">搜索</button>
    <br>
    <br>
    <table cellpadding="0" cellspacing="0" border="1px">
      <thead>
      <tr>
        <td>序号</td>
        <td>书名</td>
        <td>作者</td>
        <td>出版日期</td>
        <td>价格</td>
        <td>描述</td>
      </tr>
      </thead>
      <tbody v-for="(item, index) in books">
      <tr>
        <td>{{index+1}}</td>
        <td>{{item.name | nameType}}</td>
        <td>{{item.author}}</td>
        <td>{{item.publishDate | dateType}}</td>
        <td>{{item.price | priceType}}</td>
        <td>{{item.description}}</td>
      </tr>
      </tbody>
    </table>
  </div>
</template>

<script>
  import axios from "axios";
  import api from "@/config/api";
  import moment from "moment";

  export default {
    name: "Index",
    data() {
      return {
        books: [],
        searchName: "",
        searchAuthor: "",
        searchPriceLow: "",
        searchPriceHigh: ""
      }
    },
    methods: {
      searchBook() {
        let that = this;
        axios.post(api.GET_ALL_BOOK_BY_FUZZY).then(r => {
          that.books = r.data.tbListBook;
        })
      }
    },
    filters: {
      nameType(name) {
        return "《" + name + "》";
      },
      dateType(date) {
        return  moment(date).format('YYYY年MM月DD日');
      },
      priceType(price) {
        return "￥" + Number(price).toFixed(2);
      }
    },
    created() {
      let that = this;
      axios.post(api.GET_ALL_BOOK).then(r => {
        that.books = r.data.data.tbBookList;
      })
    }
  }
</script>

